<h2>API</h2>
<h3>Attrs/Props</h3>
<table>
<thead>
<tr>
<th>Prop</th>
<th>Type</th>
<th>Default</th>
<th>Description</th>
</tr>
</thead>
<tbody>
<tr>
<td>title</td>
<td>String</td>
<td></td>
<td>图表的标题</td>
</tr>
<tr>
<td>caption</td>
<td>String</td>
<td></td>
<td>图表的描述</td>
</tr>
<tr>
<td>data</td>
<td>Array</td>
<td></td>
<td>图表需要显示的数据</td>
</tr>
<tr>
<td>xAxis</td>
<td>Object</td>
<td></td>
<td>绘制X轴需要传入的数据，属性key的值为data数组中对象的某个属性，依据此值来绘制X轴的刻度尺, count属性用来在数据较多的情况下让x轴间隔显示</td>
</tr>
<tr>
<td>yAxis</td>
<td>Object</td>
<td></td>
<td>绘制Y轴需要传入的数据，属性min，max表示Y轴的最大值和最小值，count表示Y轴最小值和最大值之间分成几段，默认值为8</td>
</tr>
<tr>
<td>series</td>
<td>Array</td>
<td></td>
<td>传入绘制每条柱状数据的key值，key值对应着data数组中对象的属性</td>
</tr>
<tr>
<td>border</td>
<td>Boolean</td>
<td>false</td>
<td>是否有表框</td>
</tr>
<tr>
<td>legend</td>
<td>[Boolean, String]</td>
<td>false</td>
<td>X轴下方是否显示每条线段对应的标签</td>
</tr>
<tr>
<td>width</td>
<td>String</td>
<td><code>100%</code></td>
<td>图表的宽度</td>
</tr>
<tr>
<td>height</td>
<td>String</td>
<td><code>480px</code></td>
<td>图表的高度</td>
</tr>
<tr>
<td>stack</td>
<td>Boolean</td>
<td>false</td>
<td>线段是否采用堆叠方式，这种方式下series中的对象属性key等于data中标识不同部分的key字段</td>
</tr>
<tr>
<td>titleAlign</td>
<td>String</td>
<td><code>center</code></td>
<td>图表标题的对齐方式，默认是居中，值有:left,center,right</td>
</tr>
<tr>
<td>loading</td>
<td>Boolean</td>
<td><code>false</code></td>
<td>true表示正在加载中，false表示加载完成</td>
</tr>
<tr>
<td>gapSize</td>
<td>String</td>
<td><code>'normal'</code></td>
<td>控制柱的宽度，值可以是<code>mini,small,normal,medium,large,huge</code>中的任何一个</td>
</tr>
<tr>
<td>grid</td>
<td>Boolean</td>
<td><code>true</code></td>
<td>控制网格开关，默认开启网格模式</td>
</tr>
</tbody>
</table>
<h3>Events</h3>
<h4>@click</h4>
<p>选择此项前触发</p>
<table>
<thead>
<tr>
<th>Param</th>
<th>Type</th>
<th>Description</th>
</tr>
</thead>
<tbody>
<tr>
<td>$event.data</td>
<td>Object</td>
<td>当前柱的数据</td>
</tr>
<tr>
<td>$event.index</td>
<td>String</td>
<td>当前索引</td>
</tr>
</tbody>
</table>
<h3>Slots</h3>
<table>
<thead>
<tr>
<th>Slot</th>
<th>Description</th>
</tr>
</thead>
<tbody>
<tr>
<td>tooltip</td>
<td>自定义tooltip内容</td>
</tr>
</tbody>
</table>
<table>
<thead>
<tr>
<th>Slot</th>
<th>Description</th>
</tr>
</thead>
<tbody>
<tr>
<td>titleTemplate</td>
<td>自定义标题内容</td>
</tr>
</tbody>
</table>
<table>
<thead>
<tr>
<th>Slot</th>
<th>Description</th>
</tr>
</thead>
<tbody>
<tr>
<td>captionTemplate</td>
<td>自定义caption内容</td>
</tr>
</tbody>
</table>
<table>
<thead>
<tr>
<th>Slot</th>
<th>Description</th>
</tr>
</thead>
<tbody>
<tr>
<td>legendTemplate</td>
<td>自定义legend内容</td>
</tr>
</tbody>
</table>
<table>
<thead>
<tr>
<th>Slot</th>
<th>Description</th>
</tr>
</thead>
<tbody>
<tr>
<td>legendSuffix</td>
<td>自定义legend后缀内容</td>
</tr>
</tbody>
</table>
<table>
<thead>
<tr>
<th>Slot</th>
<th>Description</th>
</tr>
</thead>
<tbody>
<tr>
<td>xLabelTemplate</td>
<td>自定义X轴内容，作用域插槽，会传入三个参数<code>item</code>、<code>data</code>和<code>index</code>, 其中data是x轴的数据集合，item是当前数据项，index是当前数据项索引</td>
</tr>
</tbody>
</table>
</u-article></template>

