<h2>API</h2>
<h3>Attrs/Props</h3>
<table>
<thead>
<tr>
<th>Prop</th>
<th>Type</th>
<th>Default</th>
<th>Description</th>
</tr>
</thead>
<tbody>
<tr>
<td>title</td>
<td>String</td>
<td></td>
<td>图表的标题</td>
</tr>
<tr>
<td>caption</td>
<td>String</td>
<td></td>
<td>图表的描述</td>
</tr>
<tr>
<td>data</td>
<td>Array</td>
<td></td>
<td>图表需要显示的数据</td>
</tr>
<tr>
<td>xAxis</td>
<td>Object</td>
<td></td>
<td>绘制X轴需要传入的数据，属性key的值为data数组中对象的某个属性，依据此值来绘制X轴的刻度尺</td>
</tr>
<tr>
<td>yAxis</td>
<td>Object</td>
<td></td>
<td>绘制Y轴需要传入的数据，属性min，max表示Y轴的最大值和最小值，count表示Y轴最小值和最大值之间分成几段，默认值为8</td>
</tr>
<tr>
<td>series</td>
<td>Array</td>
<td></td>
<td>传入绘制每条线的数据，数组中对象的属性key对象data数组中对象某个属性，hidden属性表示默认隐藏此选段，absent表示不显示此选段，默认作为tooltip显示的数据内容</td>
</tr>
<tr>
<td>border</td>
<td>Boolean</td>
<td>false</td>
<td>是否有表框</td>
</tr>
<tr>
<td>legend</td>
<td>[Boolean, String]</td>
<td>false</td>
<td>X轴下方是否显示每条线段对应的标签，当值是<code>'always'</code>的时候，legend始终显示</td>
</tr>
<tr>
<td>width</td>
<td>String</td>
<td><code>100%</code></td>
<td>图表的宽度</td>
</tr>
<tr>
<td>height</td>
<td>String</td>
<td><code>480px</code></td>
<td>图表的高度</td>
</tr>
<tr>
<td>smooth</td>
<td>Boolean</td>
<td>false</td>
<td>线段是否采用平滑方式绘制</td>
</tr>
<tr>
<td>fill</td>
<td>Boolean</td>
<td>false</td>
<td>线段和X轴之间否填充</td>
</tr>
<tr>
<td>titleAlign</td>
<td>String</td>
<td><code>center</code></td>
<td>图表标题的对齐方式，默认是居中，值有:left,center,right</td>
</tr>
<tr>
<td>loading</td>
<td>Boolean</td>
<td><code>false</code></td>
<td>true表示正在加载中，false表示加载完成</td>
</tr>
<tr>
<td>scatter</td>
<td>Boolean</td>
<td><code>false</code></td>
<td>简单散点图</td>
</tr>
<tr>
<td>grid</td>
<td>Boolean</td>
<td><code>true</code></td>
<td>控制网格开关，默认开启网格模式</td>
</tr>
</tbody>
</table>
<h3>Events</h3>
<h4>@click</h4>
<p>选择此项前触发</p>
<table>
<thead>
<tr>
<th>Param</th>
<th>Type</th>
<th>Description</th>
</tr>
</thead>
<tbody>
<tr>
<td>$event.data</td>
<td>Object</td>
<td>当前点的数据</td>
</tr>
<tr>
<td>$event.index</td>
<td>String</td>
<td>当前索引</td>
</tr>
</tbody>
</table>
<h3>Slots</h3>
<table>
<thead>
<tr>
<th>Slot</th>
<th>Description</th>
</tr>
</thead>
<tbody>
<tr>
<td>tooltip</td>
<td>自定义tooltip内容</td>
</tr>
</tbody>
</table>
<table>
<thead>
<tr>
<th>Slot</th>
<th>Description</th>
</tr>
</thead>
<tbody>
<tr>
<td>operate</td>
<td>自定义内容</td>
</tr>
</tbody>
</table>
<table>
<thead>
<tr>
<th>Slot</th>
<th>Description</th>
</tr>
</thead>
<tbody>
<tr>
<td>titleTemplate</td>
<td>自定义标题内容</td>
</tr>
</tbody>
</table>
<table>
<thead>
<tr>
<th>Slot</th>
<th>Description</th>
</tr>
</thead>
<tbody>
<tr>
<td>captionTemplate</td>
<td>自定义caption内容</td>
</tr>
</tbody>
</table>
<table>
<thead>
<tr>
<th>Slot</th>
<th>Description</th>
</tr>
</thead>
<tbody>
<tr>
<td>legendTemplate</td>
<td>自定义legend内容</td>
</tr>
</tbody>
</table>
<table>
<thead>
<tr>
<th>Slot</th>
<th>Description</th>
</tr>
</thead>
<tbody>
<tr>
<td>legendSuffix</td>
<td>自定义legend后缀内容</td>
</tr>
</tbody>
</table>
<table>
<thead>
<tr>
<th>Slot</th>
<th>Description</th>
</tr>
</thead>
<tbody>
<tr>
<td>xLabelTemplate</td>
<td>自定义X轴内容，作用域插槽，会传入三个参数<code>item</code>、<code>data</code>和<code>index</code>, 其中data是x轴的数据集合，item是当前数据项，index是当前数据项索引</td>
</tr>
</tbody>
</table>
</u-article></template>

