module.exports = "<template><u-article><h1>UXbarChart 柱状图</h1>
<h2>示例</h2>
<h3>基本形式</h3>
<u-code-example
    :show-code="true"
    :show-detail="false"
    file-path="src/components/u-xbar-chart.vue/README.md">
    <div><u-xbar-chart border legend title="每星期访问量" :y-axis="{ key: 'week' }" :x-axis="{ min: 0 }" :series="[{key: 'number', name: '访问量' }]" :data="[{ week: '星期一', number: 150 }, { week: '星期二', number: 300 }, { week: '星期三', number: 28 }, { week: '星期四', number: 200 }, { week: '星期五', number: 74 }, { week: '星期六', number: 532 }, { week: '星期日', number: 420 }]"></u-xbar-chart></div>
    <div slot="code"><pre v-pre class="hljs lang-html"><code><span class="hljs-tag">&lt;<span class="hljs-name">u-xbar-chart</span> <span class="hljs-attr">border</span> <span class="hljs-attr">legend</span> <span class="hljs-attr">title</span>=<span class="hljs-string">"每星期访问量"</span> <span class="hljs-attr">:y-axis</span>=<span class="hljs-string">"{ key: 'week' }"</span> <span class="hljs-attr">:x-axis</span>=<span class="hljs-string">"{ min: 0 }"</span> <span class="hljs-attr">:series</span>=<span class="hljs-string">"[{key: 'number', name: '访问量' }]"</span> <span class="hljs-attr">:data</span>=<span class="hljs-string">"[{ week: '星期一', number: 150 }, { week: '星期二', number: 300 }, { week: '星期三', number: 28 }, { week: '星期四', number: 200 }, { week: '星期五', number: 74 }, { week: '星期六', number: 532 }, { week: '星期日', number: 420 }]"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">u-xbar-chart</span>&gt;</span></code></pre>
</div>
</u-code-example>

<h3>自定义tooltip</h3>
<u-code-example
    :show-code="false"
    :show-detail="true"
    file-path="src/components/u-xbar-chart.vue/README.md">
    <div><anondemo-09582a8f-714 /></div>
    <div slot="code"><pre v-pre class="hljs lang-vue"><code><span class="hljs-tag">&lt;<span class="hljs-name">template</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">u-xbar-chart</span> <span class="hljs-attr">border</span> <span class="hljs-attr">legend</span> <span class="hljs-attr">order</span>=<span class="hljs-string">"asc"</span> <span class="hljs-attr">:title</span>=<span class="hljs-string">"title"</span> <span class="hljs-attr">:x-axis</span>=<span class="hljs-string">"xaxis"</span> <span class="hljs-attr">:y-axis</span>=<span class="hljs-string">"yaxis"</span> <span class="hljs-attr">:series</span>=<span class="hljs-string">"series"</span> <span class="hljs-attr">:data</span>=<span class="hljs-string">"data"</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">div</span> <span class="hljs-attr">slot</span>=<span class="hljs-string">"tooltip"</span> <span class="hljs-attr">slot-scope</span>=<span class="hljs-string">"scope"</span>&gt;</span>
        {{ scope.row[scope.sery.key]}}
    <span class="hljs-tag">&lt;/<span class="hljs-name">div</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">u-xbar-chart</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">template</span>&gt;</span>

<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
<span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
    data() {
        <span class="hljs-keyword">return</span> {
            <span class="hljs-attr">title</span>: <span class="hljs-string">'每星期访问量'</span>,
            <span class="hljs-attr">yaxis</span>: { <span class="hljs-attr">key</span>: <span class="hljs-string">'week'</span> },
            <span class="hljs-attr">xaxis</span>: { <span class="hljs-attr">min</span>: <span class="hljs-number">0</span> },
            <span class="hljs-attr">series</span>: [{<span class="hljs-attr">key</span>: <span class="hljs-string">'number'</span>, <span class="hljs-attr">name</span>: <span class="hljs-string">'访问量'</span> }],
            <span class="hljs-attr">data</span>: [{ <span class="hljs-attr">week</span>: <span class="hljs-string">'星期一'</span>, <span class="hljs-attr">number</span>: <span class="hljs-number">150</span> }, { <span class="hljs-attr">week</span>: <span class="hljs-string">'星期二'</span>, <span class="hljs-attr">number</span>: <span class="hljs-number">300</span> }, { <span class="hljs-attr">week</span>: <span class="hljs-string">'星期三'</span>, <span class="hljs-attr">number</span>: <span class="hljs-number">28</span> }, { <span class="hljs-attr">week</span>: <span class="hljs-string">'星期四'</span>, <span class="hljs-attr">number</span>: <span class="hljs-number">200</span> }, { <span class="hljs-attr">week</span>: <span class="hljs-string">'星期五'</span>, <span class="hljs-attr">number</span>: <span class="hljs-number">74</span> }, { <span class="hljs-attr">week</span>: <span class="hljs-string">'星期六'</span>, <span class="hljs-attr">number</span>: <span class="hljs-number">532</span> }, { <span class="hljs-attr">week</span>: <span class="hljs-string">'星期日'</span>, <span class="hljs-attr">number</span>: <span class="hljs-number">420</span> }],
        }
    }
};
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span></code></pre>
</div>
</u-code-example>

<h4>堆叠数据</h4>
<u-code-example
    :show-code="false"
    :show-detail="true"
    file-path="src/components/u-xbar-chart.vue/README.md">
    <div><anondemo-bc33c016-904 /></div>
    <div slot="code"><pre v-pre class="hljs lang-vue"><code><span class="hljs-tag">&lt;<span class="hljs-name">template</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">u-xbar-chart</span> <span class="hljs-attr">border</span> <span class="hljs-attr">legend</span> <span class="hljs-attr">:stack</span>=<span class="hljs-string">"stack"</span> <span class="hljs-attr">:title</span>=<span class="hljs-string">"title"</span> <span class="hljs-attr">:x-axis</span>=<span class="hljs-string">"xaxis"</span> <span class="hljs-attr">:y-axis</span>=<span class="hljs-string">"yaxis"</span> <span class="hljs-attr">:series</span>=<span class="hljs-string">"series"</span> <span class="hljs-attr">:data</span>=<span class="hljs-string">"data"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">u-xbar-chart</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">template</span>&gt;</span>

<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
<span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
    data() {
        <span class="hljs-keyword">return</span> {
            <span class="hljs-attr">title</span>: <span class="hljs-string">'每星期访问量'</span>,
            <span class="hljs-attr">yaxis</span>: { <span class="hljs-attr">key</span>: <span class="hljs-string">'week'</span> },
            <span class="hljs-attr">xaxis</span>: { <span class="hljs-attr">min</span>: <span class="hljs-number">0</span> },
            <span class="hljs-attr">stack</span>: <span class="hljs-string">'stack'</span>,
            <span class="hljs-attr">series</span>: [{ <span class="hljs-attr">key</span>: <span class="hljs-string">'rds'</span> }, { <span class="hljs-attr">key</span>: <span class="hljs-string">'ncr'</span> }, { <span class="hljs-attr">key</span>: <span class="hljs-string">'nce'</span> }],
            <span class="hljs-attr">data</span>: [
                { <span class="hljs-attr">week</span>: <span class="hljs-string">'星期一'</span>, <span class="hljs-attr">rds</span>: <span class="hljs-number">150</span>, <span class="hljs-attr">ncr</span>: <span class="hljs-number">200</span>, <span class="hljs-attr">nce</span>: <span class="hljs-number">50</span> },
                { <span class="hljs-attr">week</span>: <span class="hljs-string">'星期二'</span>, <span class="hljs-attr">rds</span>: <span class="hljs-number">300</span>, <span class="hljs-attr">ncr</span>: <span class="hljs-number">340</span>, <span class="hljs-attr">nce</span>: <span class="hljs-number">20</span> },
                { <span class="hljs-attr">week</span>: <span class="hljs-string">'星期三'</span>, <span class="hljs-attr">rds</span>: <span class="hljs-number">28</span>, <span class="hljs-attr">ncr</span>: <span class="hljs-number">56</span>, <span class="hljs-attr">nce</span>: <span class="hljs-number">28</span> },
                { <span class="hljs-attr">week</span>: <span class="hljs-string">'星期四'</span>, <span class="hljs-attr">rds</span>: <span class="hljs-number">200</span>, <span class="hljs-attr">ncr</span>: <span class="hljs-number">78</span>, <span class="hljs-attr">nce</span>: <span class="hljs-number">40</span> },
                { <span class="hljs-attr">week</span>: <span class="hljs-string">'星期五'</span>, <span class="hljs-attr">rds</span>: <span class="hljs-number">74</span>, <span class="hljs-attr">ncr</span>: <span class="hljs-number">100</span>, <span class="hljs-attr">nce</span>: <span class="hljs-number">74</span> },
                { <span class="hljs-attr">week</span>: <span class="hljs-string">'星期六'</span>, <span class="hljs-attr">rds</span>: <span class="hljs-number">532</span>, <span class="hljs-attr">ncr</span>: <span class="hljs-number">200</span>, <span class="hljs-attr">nce</span>: <span class="hljs-number">32</span> },
                { <span class="hljs-attr">week</span>: <span class="hljs-string">'星期日'</span>, <span class="hljs-attr">rds</span>: <span class="hljs-number">420</span>, <span class="hljs-attr">ncr</span>: <span class="hljs-number">260</span>, <span class="hljs-attr">nce</span>: <span class="hljs-number">20</span> },
            ],
        }
    }
};
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span></code></pre>
</div>
</u-code-example>

<h2>API</h2>
<h3>Attrs/Props</h3>
<table>
<thead>
<tr>
<th>Prop</th>
<th>Type</th>
<th>Default</th>
<th>Description</th>
</tr>
</thead>
<tbody>
<tr>
<td>title</td>
<td>String</td>
<td></td>
<td>图表的标题</td>
</tr>
<tr>
<td>caption</td>
<td>String</td>
<td></td>
<td>图表的描述</td>
</tr>
<tr>
<td>data</td>
<td>Array</td>
<td></td>
<td>图表需要显示的数据</td>
</tr>
<tr>
<td>yAxis</td>
<td>Object</td>
<td></td>
<td>绘制Y轴需要传入的数据，属性key的值为data数组中对象的某个属性，依据此值来绘制X轴的刻度尺</td>
</tr>
<tr>
<td>xAxis</td>
<td>Object</td>
<td></td>
<td>绘制X轴需要传入的数据，属性min，max表示Y轴的最大值和最小值，count表示Y轴最小值和最大值之间分成几段，默认值为8</td>
</tr>
<tr>
<td>series</td>
<td>Array</td>
<td></td>
<td>传入绘制每条柱状数据的key值，key值对应着data数组中对象的属性</td>
</tr>
<tr>
<td>border</td>
<td>Boolean</td>
<td>false</td>
<td>是否有表框</td>
</tr>
<tr>
<td>legend</td>
<td>Boolean</td>
<td>false</td>
<td>X轴下方是否显示每条线段对应的标签</td>
</tr>
<tr>
<td>width</td>
<td>String</td>
<td><code>100%</code></td>
<td>图表的宽度</td>
</tr>
<tr>
<td>height</td>
<td>String</td>
<td><code>480px</code></td>
<td>图表的高度</td>
</tr>
<tr>
<td>order</td>
<td>String</td>
<td><code>desc</code></td>
<td>排序顺序，默认按照升序排序，顺序是从上到下，按照传入的数据采用传入desc,asc之外的值即可</td>
</tr>
<tr>
<td>stack</td>
<td>Boolean</td>
<td>false</td>
<td>线段是否采用堆叠方式，这种方式下series中的对象属性key等于data中标识不同部分的key字段</td>
</tr>
<tr>
<td>titleAlign</td>
<td>String</td>
<td><code>center</code></td>
<td>图表标题的对齐方式，默认是居中，值有:left,center,right</td>
</tr>
<tr>
<td>loading</td>
<td>Boolean</td>
<td><code>false</code></td>
<td>true表示正在加载中，false表示加载完成</td>
</tr>
<tr>
<td>grid</td>
<td>Boolean</td>
<td><code>true</code></td>
<td>控制网格开关，默认开启网格模式</td>
</tr>
</tbody>
</table>
<h3>Slots</h3>
<table>
<thead>
<tr>
<th>Slot</th>
<th>Description</th>
</tr>
</thead>
<tbody>
<tr>
<td>tooltip</td>
<td>自定义tooltip内容</td>
</tr>
</tbody>
</table>
<table>
<thead>
<tr>
<th>Slot</th>
<th>Description</th>
</tr>
</thead>
<tbody>
<tr>
<td>titleTemplate</td>
<td>自定义标题内容</td>
</tr>
</tbody>
</table>
<table>
<thead>
<tr>
<th>Slot</th>
<th>Description</th>
</tr>
</thead>
<tbody>
<tr>
<td>captionTemplate</td>
<td>自定义caption内容</td>
</tr>
</tbody>
</table>
</u-article></template>

<script>
    import Component0 from "./anondemo-09582a8f-714.vue";
import Component1 from "./anondemo-bc33c016-904.vue";

    export default {
        components: {
'anondemo-09582a8f-714': Component0,
'anondemo-bc33c016-904': Component1,
},
    }
</script>
            "


